.coinTable {
  width: 90vw;
  margin-top: 32px;
  margin-bottom: 80px;
}

table {
  border: 1px solid black;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: fixed;
  color: black;
}

table tr {
  border: 1px solid black;
  padding: 0.35em;
}

table th,
table td {
  padding: 0.625em;
  text-align: center;
}

table th {
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

th {
  font-size: 1.6rem;
}
tr {
  font-size: 1.2rem;
}

@media screen and (max-width: 1400px) {
  table {
    border: 0;
  }

  table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }

  table tr {
    border-bottom: 3px solid black;
    display: block;
    margin-bottom: 0.625em;
  }

  table td {
    border-bottom: 1px solid black;
    display: block;
    font-size: 0.8em;
    text-align: right;
  }

  table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
    color: black;
  }

  table td:last-child {
    border-bottom: 0;
  }
}

@media screen and (max-width: 720px) {
  .coinTable {
    width: 70vw;
  }
}
